"use client";

import { useEffect, useState } from "react";
import {
  FaHeartbeat,
  FaTint,
  FaThermometerHalf,
  FaLungs,
  FaBurn,
  FaCog,
  FaBell,
} from "react-icons/fa";
import VitalsCard from "../components/dashboard/VitalsCard";
import HeartRateChart from "../components/dashboard/HeartRateChart";
import BottomNav from "../components/dashboard/BottomNav"; // ‚úÖ Import BottomNav
import { useRouter } from "next/navigation";

export default function Dashboard() {
  const router = useRouter();
  const [currentBpm, setCurrentBpm] = useState(72);
  const [bpmData, setBpmData] = useState<{ time: string; bpm: number }[]>([]);
  const [userName] = useState("John Doe");
  const [sweatTemp] = useState("92.3 ¬∞F");
  const [pulseRate] = useState(76);

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentBpm((prev) => {
        const newBpm =
          prev +
          (Math.random() > 0.5 ? 1 : -1) * Math.floor(Math.random() * 5);
        return Math.max(60, Math.min(110, newBpm));
      });
      setBpmData((prev) => [
        ...prev.slice(-9),
        { time: new Date().toLocaleTimeString().slice(0, 5), bpm: currentBpm },
      ]);
    }, 3000);
    return () => clearInterval(interval);
  }, [currentBpm]);

  const heartRisk =
    currentBpm > 120 ? "‚ö†Ô∏è High Heart Rate" : "‚úÖ Normal Heart Rate";
  const feverRisk =
    parseFloat("98.6") > 100
      ? "ü§í Fever Detected"
      : "‚úÖ Normal Body Temperature";
  const oxygenStatus = 98 < 95 ? "‚ö†Ô∏è Low Oxygen Level" : "‚úÖ Oxygen Normal";

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-500 via-purple-500 to-indigo-700 flex flex-col px-6 py-6">
      {/* Header */}
      <div className="rounded-2xl p-5 flex justify-between items-center shadow-lg text-white bg-gradient-to-r from-blue-500 to-purple-600">
        {/* Left Side - App Name */}
        <div>
          <h1 className="text-2xl font-bold">BeatAware</h1>
          <p className="mt-1 text-sm italic opacity-90">
            ‚ÄúListen to your heart, BeatAware helps you care.‚Äù üíô
          </p>
        </div>

        {/* Right Side - User Info Card */}
        <div className="flex items-center gap-3 bg-gradient-to-r from-white to-gray-100 text-gray-800 px-4 py-2 rounded-2xl shadow-lg border">
          {/* Avatar Circle */}
          <div className="w-10 h-10 flex items-center justify-center bg-gradient-to-r from-blue-400 to-purple-500 text-white font-bold rounded-full shadow">
            {userName.charAt(0)}
          </div>

          {/* User Info */}
          <div className="flex flex-col leading-tight">
            <span className="font-semibold">{userName}</span>
            <span className="text-xs text-gray-500">Active User</span>
          </div>

          {/* Action Icons */}
          <div className="flex items-center gap-2 ml-3">
            <button className="w-8 h-8 flex items-center justify-center rounded-full bg-gray-200 hover:bg-blue-100 text-blue-600 transition">
              <FaBell size={14} />
            </button>
            <button className="w-8 h-8 flex items-center justify-center rounded-full bg-gray-200 hover:bg-purple-100 text-purple-600 transition">
              <FaCog size={14} />
            </button>
          </div>
        </div>
      </div>

      {/* Vitals Cards */}
      <div className="grid grid-cols-2 gap-6 mt-6">
        <VitalsCard
          label="Heart Rate"
          value={currentBpm}
          unit="bpm"
          icon={<FaHeartbeat />}
        />
        <VitalsCard
          label="Blood Pressure"
          value="120/80"
          unit="mmHg"
          icon={<FaTint />}
        />
        <VitalsCard
          label="Body Temp."
          value="98.6"
          unit="¬∞F"
          icon={<FaThermometerHalf />}
        />
        <VitalsCard
          label="Oxygen Saturation"
          value="98"
          unit="%"
          icon={<FaLungs />}
        />
        <VitalsCard
          label="Pulse Rate"
          value={pulseRate}
          unit="bpm"
          icon={<FaHeartbeat />}
        />
        <VitalsCard
          label="Sweat Temp."
          value={sweatTemp}
          icon={<FaBurn />}
        />
      </div>

      {/* Heart Rate Chart */}
      <div className="bg-white rounded-2xl p-5 shadow-lg mt-6">
        <h3 className="text-lg font-semibold mb-3 text-black">Heart Rate Trend</h3> {/* ‚úÖ Black heading */}
        <HeartRateChart data={bpmData} />
      </div>

      {/* Health Summary */}
      <div className="bg-white rounded-2xl p-5 shadow-lg mt-6">
        <h3 className="text-lg font-semibold mb-3 text-black">Health Summary</h3> {/* ‚úÖ Black heading */}
        <ul className="space-y-2 text-gray-800">
          <li>{heartRisk}</li>
          <li>{feverRisk}</li>
          <li>{oxygenStatus}</li>
        </ul>
      </div>

      {/* Bottom Navigation (imported) */}
      <BottomNav /> {/* ‚úÖ Using imported component */}
    </div>
  );
}
